  export default {
	name: 'FoodCategory',
	mounted:function(){
		var self = this;
	},
	methods:{
		showOrderForm: function(item){
			var self = this;
			self.current_view.item = item;
			self.current_view.chose_additions = [];
			self.current_view.chose_combo = false;
			self.current_view.chose_combo_options = [];
			self.current_view._expanded_additions = false;
			self.current_view.remark = "";
			self.order_modal = true;
		},
		addToCart: function(){
			var self = this;
			self.current_view.item.total_price = self.current_view_total_price;
			self.current_view.item.message = "";
			self.current_view.item.message += self.current_view.item.title+"($"+self.current_view.item.price+")<br>";
			if(typeof self.current_view.item.chose_option != 'undefined'){
				self.current_view.item.message += "轉"+self.current_view.item.chose_option+"<br>";
			}
			if(self.current_view.chose_additions.length > 0){
				self.current_view.item.message += "加配料<br>";
			}
			for(var addition of self.current_view.chose_additions){
				self.current_view.item.message += self.api.additions[addition].title+"($"+self.api.additions[addition].price+")"+"<br>";
			}
			if(self.current_view.chose_combo){
				self.current_view.item.message += "跟餐($"+self.api.combo.price+")<br>";
				for(var [i,chose_combo_option] of self.current_view.chose_combo_options.entries()){
					self.current_view.item.message += self.api.combo.groups[i].title+":"+self.api.combo.groups[i].options[chose_combo_option].title+"($"+self.api.combo.groups[i].options[chose_combo_option].price+")"+"<br>";
				}
			}
			if(self.current_view.remark!=""){
				self.current_view.item.message += "食物備註："+self.current_view.remark+"<br>";
			}
			self.current_view.item.message += "共$"+self.current_view.item.total_price;
			// self.current_view.item.remark = self.current_view.remark;
			self.cart_items.push(self.current_view.item);

			self.order_modal = false;
		},
		showCart: function(){
			var self = this;
			self.cart_modal = true;
		},
		whatsappOrder: function(mobile){
			var self = this;
			window.open("https://wa.me/"+mobile+"?text="+self.order_message_with_0A);
		},
		telegramOrder: function(target){
			var self = this;
			var copyText = document.getElementById("orderMessage");
			copyText.select();
  			copyText.setSelectionRange(0, 99999); 
			document.execCommand("copy");
			alert("訂單內容已複製在剪貼薄，在Telegram手動貼上文字");
			window.open("https://t.me/"+target);
		}
	},
	computed:{
		current_view_total_price: function(){
			var self = this;
			var total = 0;
			total += self.current_view.item.price;
			if(self.current_view.chose_combo){ 
				total += self.api.combo.price;
				for(var [i, chose_combo_option] of self.current_view.chose_combo_options.entries()){
					if (typeof chose_combo_option !== 'undefined') {
						total += self.api.combo.groups[i].options[chose_combo_option].price;
					}
				}
			}
			for(var i of self.current_view.chose_additions){
				total += self.api.additions[i].price;
			}
			return total;
		},
		cart_total_price: function(){
			var self = this;
			var total = 0;
			for(var item of self.cart_items){
				total += item.total_price;
			}
			return total;
		},
		order_message: function(){
			var self = this;
			var order_message = "";
			order_message += self.config.order_prefix+"<br>";
			order_message += "餐廳名："+self.config.shop_name+"<br>";
			order_message += "共"+self.cart_items.length+"款食物：<br>";
			for(var item of self.cart_items){
				order_message += item.message+"<br>";
			}
			order_message += "總共$"+self.cart_total_price+"<br>";
			order_message += "聯絡方式：<br>";
			order_message += "送餐地址：<br>";
			order_message += "訂單備註：<br>";
			return order_message;
		},
		order_message_with_breakline: function(){
			var self = this;
			return self.order_message.split("<br>").join("\n");
		},
		order_message_with_0A: function(){
			var self = this;
			return self.order_message.split("<br>").join("%0A");
		}
	},
